html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html,
body {
  height: 100%; }

[data-elm-hot="true"] {
  height: inherit; }

body {
  font-family: 'Source Sans Pro', 'Trebuchet MS', 'Lucida Grande', 'Bitstream Vera Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  text-align: center;
  color: #293c4b; }

h1 {
  font-size: 30px; }

img {
  margin: 20px 0;
  max-width: 200px; }

table.board {
  border-collapse: collapse;
  margin: 0 auto; }

table tr td {
  width: 8vw;
  height: 8vw;
  padding: 0;
  margin: 0;
  overflow: hidden;
  background-image: url("./images/space.gif");
  background-size: 8vw;
  background-repeat: no-repeat; }
  table tr td span {
    display: block;
    padding: 0;
    margin: 0; }

td.portal {
  background-image: url("./images/portal1.gif"); }
  td.portal.has-particle .particle {
    display: none; }

td.black-hole {
  background-image: url("./images/blackHole.gif"); }
  td.black-hole.has-particle .particle {
    display: none; }

td.change-direction {
  background-image: url("./images/arrow.gif"); }

.change-direction-up {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg); }
  .change-direction-up > * {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg); }

.change-direction-right {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg); }
  .change-direction-right > * {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg); }

.change-direction-down {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg); }
  .change-direction-down > * {
    -webkit-transform: rotate(-270deg);
            transform: rotate(-270deg); }

td.mirror {
  background-image: url("./images/reverse.gif"); }

td.energizer {
  background-image: url("./images/energizer.gif"); }

td.mirror-left {
  background-image: url("./images/mirror.gif");
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg); }

td.mirror-right {
  background-image: url("./images/mirror.gif"); }

td.cluster-1 {
  background-image: url("./images/oneCore.gif"); }
  td.cluster-1.has-particle {
    background-image: url("./images/oneToTwoCore.gif"); }
    td.cluster-1.has-particle .particle {
      display: none; }

td.cluster-2 {
  background-image: url("./images/twoCore.gif"); }
  td.cluster-2.has-particle {
    background-image: url("./images/twoToThreeCore.gif"); }
    td.cluster-2.has-particle .particle {
      display: none; }

td.cluster-3 {
  background-image: url("./images/threeCore.gif"); }
  td.cluster-3.has-particle {
    background-image: url("./images/threeToFourCore.gif"); }
    td.cluster-3.has-particle .particle {
      display: none; }

td.cluster-4 {
  background-image: url("./images/fourCore.gif"); }
  td.cluster-4.has-particle {
    background-image: url("./images/fourExplosion.gif"); }
    td.cluster-4.has-particle .particle {
      display: none; }

.particle {
  display: inline-block;
  background-image: url("./images/projectile2.gif");
  background-size: 2vw;
  width: 2vw;
  height: 4vw; }

.particle-up {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg); }

.particle-left {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg); }

.particle-right {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg); }

.particle-down {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg); }
